@function define_icon(
    $ic_name,
    $fill_color: black,
    $stroke_color: transparent,
    $stroke_width: 0,
    $extra_css: '' ) {

  $ic_path: _get_icon_path($ic_name);
  $icon: '<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink" width="1792" height="1792" viewBox="0 0 1792 1792"><path fill="#{$fill_color}" stroke="#{$stroke_color}" stroke-width="#{$stroke_width}" d="#{$ic_path}" /></svg>';

  @return url('data:image/svg+xml;charset=utf-8,#{url-encode($icon)}');
}


// DEFAULT ICON PLACEHOLDER
%svg_icon {
  display: inline-block;
  font-size: 0;
  width: 50px;
  height: 50px;
  margin: 10px 20px;
  background-size: 80%;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  transition: color 200ms linear 0ms;
  cursor: pointer;
  vertical-align: middle;
}

// DEFAULT ICON HOVER EFFECT PLACEHOLDER
%svg_icon_hover {
  background-color: $black;
  transform: scale(1.15, 1.15) rotate(-5deg);
  transition: .3s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

%custom_svg_icons {
  display: inline-block;
  margin: auto;
  padding: 0;
  width: 100%;
  height: 100%;
  background-size: contain;
  background-repeat: no-repeat;
  background-position: 50% 50%;
  cursor: pointer;
}
