@import 'theme_config';
@import "theme_icons";
@import "theme_elements";

$spotify_black: #191414;

body {
  background: $spotify_black;
  color: $white_10;
  text-align: left;
}

.track {
  margin: .7em 0;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  
  .add_track {
    font-size: 0.8em;
    min-width: 50px;
    margin-right: 1em;
  }
  
  .track-added-at {
    color: #54f16e;
    font-size: 0.8em;
    position: absolute;
    right: 0;
  }
}

.spot_button {
  background-color: #2ebd59;
  border-radius: 2em;
  border: 0;
  color: #fff;
  display: block;
  font-size: 1em;
  letter-spacing: 1.2px;
  line-height: 1.5;
  padding: .4em 1em .2em;
  transition: background-color .15s ease,border-color .15s ease,color .15s ease;
  min-width: 130px;
  text-transform: uppercase;
  white-space: normal;
  
  &--small {
    font-size:  .8em;
    min-width: 50px;
  }
}


.search_form {
  &__input {
    display: block;
    border: 0;
    border-radius: 1em;
    box-shadow: none;
    transition: border-color ease-in-out .15s,box-shadow ease-in-out .15s;
    touch-action: manipulation;
    -webkit-appearance: none;
    height: 40px;
    padding: 6px 12px;
    width: 100%;
    font-size: 16px;
    line-height: 1.5;
    color: #2e2f33;
    background-color: #fff;
  }
  
  &__submit {
    position: absolute;
    right: 1.2em;
    color: #fff;
    background-color: #2ebd59;
    font-size: 1em;
    letter-spacing: 1.2px;
    line-height: 1.5;
    border-radius: 0em 1em 1em 0em;
    padding: 0.6em 0.5em 0.4em;
    transition: background-color .15s ease, border-color .15s ease,color .15s ease;
    z-index: 1;
    border-width: 0;
    min-width: 110px;
    text-transform: uppercase;
    white-space: normal;
  }
}


.spot_profile_widget {
  overflow: hidden;
  zoom: 1;
  
  a {
    border: 0;
    font: inherit;
    font-size: 100%;
    margin: 0;
    padding: 0;
    vertical-align: baseline;
  }
  
  
  .img {
    height: 56px;
    overflow: hidden;
    position: relative;
    width: 56px;
    background-size: cover;
    background-position: 50% 50%;
    float: left;
    margin-right: 10px;
    border-radius: 56px;
  
    &::before {
      box-shadow: inset 0 0 2px hsla(45,5%,83%,.8);
      content: "";
      display: block;
      height: 100%;
      position: absolute;
      width: 100%;
      z-index: 1;
    }
  }
  
  .bd {
    padding-top: 3px;
  }
  
  h1 {
    font-size: 14px;
    margin: 7px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;

    a {
      color: #eee;
    }
  }
  
  button {
    background: #2ebd59;
    border-radius: 500px;
    border: 0;
    color: #fff;
    cursor: pointer;
    float: left;
    font-size: 11px;
    line-height: 11px;
    margin: 0;
    margin-right: 10px;
    min-width: 92px;
    padding-bottom: 3px;
    padding-top: 2px;
    position: relative;
    text-align: center;
    white-space: nowrap;
  }
  
  .bt-icon {
    background-image: url(/open-playlist/assets/Spotify_Icon_White.png);
    background-size: contain;
    display: inline-block;
    height: 17px;
    position: absolute;
    top: 3px;
    width: 17px;
    left: 3px;
  }
  
  .bt-text {
    display: block;
    text-transform: uppercase;
    font-weight: 200;
    letter-spacing: 1px;
    margin-left: 13px;
    padding: 6px 4px 1px 8px;
  }

  
  
  .count {
    -webkit-perspective: 600px;
    perspective: 600px;
    display: inline-block;
  }
  
      .count-num.front {
      -webkit-transform: rotateX(0deg) rotateY(0deg);
      transform: rotateX(0deg) rotateY(0deg);
      -webkit-transform-style: preserve-3d;
      transform-style: preserve-3d;
      position: absolute;
      z-index: 900;
    }
      
    .count-num {
    background: #fff;
    border: 1px solid #d6d5d2;
    border-radius: 3px;
    color: #999896;
    line-height: 10px;
    min-width: 34px;
    padding: 6px 8px;
    text-align: center;
    white-space: nowrap;
    margin-left: -1px;
    }
      
      
    .count-num.back, .count-num.front {
    -webkit-transition: -webkit-transform .4s ease-in-out;
    transition: -webkit-transform .4s ease-in-out;
    transition: transform .4s ease-in-out;
    transition: transform .4s ease-in-out,-webkit-transform .4s ease-in-out;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    }

    .count-num.back {
      -webkit-transform: rotateX(-180deg);
      transform: rotateX(-180deg);
      -webkit-transform-style: preserve-3d;
      transform-style: preserve-3d;
      z-index: 800;
    }

    .count-num:after, .count-num:before {
      border: solid transparent;
      content: " ";
      height: 0;
      position: absolute;
      width: 0;
      right: 100%;
    }
    .count-num:before {
    border-right-color: #d6d5d2;
    border-width: 6px;
    top: 6px;
    }

    .count-num:after, .count-num:before {
      border: solid transparent;
      content: " ";
      height: 0;
      position: absolute;
      width: 0;
      right: 100%;
    }
    .count-num:after {
      border-width: 4.5px;
      top: 7.5px;
      border-right-color: #fff;
    }
    .count-num:before {
      border-right-color: #d6d5d2;
      border-width: 6px;
      top: 6px;
    }
}
